import { Component } from "react";
import AppInfo from "../app-info/app-info";
import SearchPanel from "../search-panel/search-panel";
import AppFilter from "../app-filter/app-filter";
import EmployeesList from "../employees-list/employees-list";
import EmployeesAddForm from "../employees-add-form/employees-add-form";
import Props from "../props/props";

import "./app.css";

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      data: [
        { name: "Jon", salary: 1500, increase: false, star: true, id: 1 },
        { name: "Tom", salary: 2000, increase: true, star: false, id: 2 },
        { name: "Mike", salary: 4000, increase: false, star: false, id: 3 },
        { name: "Mihael", salary: 3000, increase: false, star: false, id: 4 },
      ],
      term: '',
      filter: 'all',
    };
    this.maxId = 5;
  }

  deleteItem = (id) => {
    this.setState(({ data }) => {
      return {
        data: data.filter((item) => item.id !== id),
      };
    });
  };

  addItem = (name, salary) => {
    const newItem = {
      name,
      salary,
      increase: false,
      star: false,
      id: this.maxId++,
    };
    this.setState(({ data }) => {
      const newArr = [...data, newItem];
      return {
        data: newArr,
      };
    });
  };

  toggleProp = (id, prop) => {
    this.setState(({ data }) => ({
      data: data.map(item => {
        if(item.id === id) {
          return {...item, [prop]: !item[prop]}
        }
        return item;
      })
    }));
  };

  searchData = (items, term) => {
    if(term.lenght === 0) {
      return items;
    }
    return items.filter(item => {
      return item.name.indexOf(term) > -1
    })
  }

  uppdateSearch = (term) => {
      this.setState({term: term})
  }

  filterPost = (items, filter) => {
    switch(filter) {
      case 'star':
        return items.filter(item => item.star)
      case 'moreThen1000':
        return items.filter(item => item.salary > 1000);
      default:
        return items    
    }
  }
  onFilterSelect = (filter) => {
    this.setState({filter})
  }

  

  render() {
      const {data, term, filter} = this.state;
      const numEmploy = this.state.data.length;
      const increased = this.state.data.filter(item => item.increase).length;
      const visibleData = this.filterPost(this.searchData(data, term), filter);

    return (
      <div className="app">
        <AppInfo 
          numEmploy={numEmploy}
          increased={increased}
        />

        <div className="search-panel">
          <SearchPanel
          uppdateSearch={this.uppdateSearch} />
          <AppFilter
          filter={filter}
          onFilterSelect={this.onFilterSelect} />
        </div>

        <EmployeesList 
        data={visibleData} 
        onDel={this.deleteItem}
        toggleProp={this.toggleProp}
         />

        <EmployeesAddForm addItem={this.addItem} />
        <Props />
      </div>
    );
  }
}

export default App;
